import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Platform } from 'ionic-angular';
import { ImageViewerController } from 'ionic-img-viewer';

/**
 * Generated class for the VsectionPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-vsection',
  templateUrl: 'vsection.html',
})
export class VsectionPage {

       _imageViewerCtrl: ImageViewerController;
 
  constructor(public navCtrl: NavController, imageViewerCtrl: ImageViewerController, public navParams: NavParams, public platform: Platform) {
  
    this._imageViewerCtrl = imageViewerCtrl;
    
    let backAction =  platform.registerBackButtonAction(() => {
        console.log("contact");
        this.navCtrl.pop();
        backAction();
      },2)
    
  }
 
  presentImage(myImage) {
    const imageViewer = this._imageViewerCtrl.create(myImage);
    imageViewer.present();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad VsectionPage');
  }
  
  
 

}



